    "use client";
    import React, { useState } from "react";

    export default function App() {
      const [query, setQuery] = useState("");
      const [data, setData] = useState(null);
      const [loading, setLoading] = useState(false);
      const [error, setError] = useState("");

      const handleSubmit = async (e) => {
        e.preventDefault();
        if (!query.trim()) return;
        setLoading(true);
        setError("");
        setData(null);

        try {
          const response = await fetch(
            `http://localhost:8000/ask?query=${encodeURIComponent(query)}`
          );

          if (!response.ok) throw new Error("–û—à–∏–±–∫–∞ —Å–µ—Ä–≤–µ—Ä–∞");

          const result = await response.json();
          setData(result);
        } catch (err) {
          setError(err.message);
        } finally {
          setLoading(false);
        }
      };

      return (
        <div className="relative flex h-screen">

          {/* üî• –ê–Ω–∏–º–∏—Ä–æ–≤–∞–Ω–Ω—ã–π —Ñ–æ–Ω */}
        <div
  className="
    absolute inset-0
    -top-1/2 -left-1/2
    w-[300%] h-[300%]
    rounded-full
    animate-[gradient-rotate_28s_linear_infinite]
    bg-[conic-gradient(at_top_left,_#ffffff,_#d9ede5,_#bdd8cf,_#ffffff)]
    opacity-90
    pointer-events-none
  "
/>

          {/* –ö–æ–Ω—Ç–µ–Ω—Ç –ø–æ–≤–µ—Ä—Ö */}
          <div className="relative flex h-full w-full">

            {/* Sidebar */}
            <aside className="w-64 bg-white/20 backdrop-blur-2xl border-r border-white/30 p-6 flex flex-col gap-6 rounded-r-3xl shadow-lg">
              <div className="text-2xl font-semibold text-[#2d6658] drop-shadow-sm">
                AI Pharmacist
              </div>

              <button className="bg-white/50 backdrop-blur-xl px-4 py-3 rounded-2xl shadow-md hover:bg-white/60 transition">
                –ù–æ–≤—ã–π —á–∞—Ç
              </button>
            </aside>

            {/* Main */}
            <main className="flex-1 p-12 text-[#1d3b33]">
              <h1 className="text-3xl mb-10 drop-shadow-sm">–í–∞—Å –ø—Ä–∏–≤–µ—Ç—Å—Ç–≤—É–µ—Ç AI Pharmacist!</h1>

              {/* Input */}
              <form
                onSubmit={handleSubmit}
                className="flex w-[470px] bg-white/25 backdrop-blur-3xl px-6 py-3 rounded-full shadow-md border border-white/40"
              >
                <input
                  type="text"
                  className="flex-1 bg-transparent outline-none text-lg placeholder-[#1d3b33]/40"
                  value={query}
                  onChange={(e) => setQuery(e.target.value)}
                  placeholder="–£–∑–Ω–∞—Ç—å —É AI Pharmacist"
                />

                <button className="text-xl hover:opacity-70 transition">‚û§</button>
              </form>

              {loading && <p className="mt-6 text-lg">–ó–∞–≥—Ä—É–∑–∫–∞...</p>}
              {error && <p className="mt-6 text-lg text-red-600">–û—à–∏–±–∫–∞: {error}</p>}

              {data && (
                <div className="mt-10">
                  <h2 className="text-2xl font-semibold">–û—Ç–≤–µ—Ç:</h2>
                  <p className="mt-4 text-lg leading-relaxed">{data.answer}</p>

                  {data.sources && data.sources.length > 0 && (
                    <>
                      <h3 className="text-xl mt-6 font-medium">–ò—Å—Ç–æ—á–Ω–∏–∫–∏:</h3>
                      <ul className="list-disc ml-6 mt-2">
                        {data.sources.map((src, idx) => (
                          <li key={idx}>
                            <a
                              href={src.url}
                              target="_blank"
                              rel="noopener noreferrer"
                              className="text-blue-700 underline"
                            >
                              {src.name}
                            </a>
                          </li>
                        ))}
                      </ul>
                    </>
                  )}
                </div>
              )}
            </main>
          </div>
        </div>
      );
    }
